import React, { useEffect, memo } from 'react';
import {Table} from 'react-bootstrap';
import { useDispatch } from 'react-redux';//
import { useSelector } from 'react-redux';//
import { connect } from 'react-redux';

function Cart(props){

    let state = useSelector((state)=> state );

    let dispatch = useDispatch();

    console.log(state);

    let plus = function(i) { dispatch({ type: '수량증가', payload: { id: i} }); };

    return(
        <div>

            <div>
                <Table responsive="sm">
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>상품명</th>
                            <th>수량</th>
                            <th>변경</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            state.reducer.map((s, i)=>{

                                return (
                                    <tr key={i}>
                                        <td>{ s.id }</td>
                                        <td>{ s.name }</td>
                                        <td>{ s.quan }</td>
                                        {/* 데이터수정을 요청할 땐 props.dispatch() */}
                                        <td>
                                            <button onClick={()=>{ plus(i) }}>➕</button>
                                            <button onClick={()=>{ dispatch({ type: '수량감소', payload: { id: i} }); }}>➖</button>
                                        </td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </Table>
                {
                    props.showsAlert ? 
                        <div className='my-alert2'>
                            <p>지금 구매하시면 신규할인 20%</p>
                            <button onClick={()=>{ dispatch({ type: '클릭' }) }}>닫기</button>
                        </div>
                        // dispatch()로 수정요청할 때 데이터를 보낼 수 있다.
                        // payload:  보낼데이터
                    : null
                }
                <Parent 이름="rami11" 나이="11"></Parent>
            
            </div>
        </div>
    )
}

