function Parent(props) {
    return (
        <div>
            <Child1 이름={props.이름}></Child1>
            <Child2 나이={props.나이}></Child2>
        </div>
    )
}

function Child1(){
    useEffect(()=>{ console.log('렌더링됨1')});
    return <div>111</div>
}
